<template>
  <v-stepper v-model="currentStep">
    <camera-dialog @captureClose="onCaptureClose" @cancelClose="onCancelClose"></camera-dialog>

    <v-stepper-items
      v-touch="{
        left: () => swipe('Left'),
        right: () => swipe('Right'),
      }"
    >
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >
      <v-layout row fill-height>
        <v-flex xs12>
          <span><v-btn class="headline" color="primary" @click="showDialog(true)">{{ $t("checkIn.titleId") }}</v-btn></span>
          <span v-if="member.idVerified">{{ $t( "preCheckIn.idVerified") }}</span>
        </v-flex>
      </v-layout>  
      <v-layout row fill-height>
        <v-flex xs12>
          <v-container fluid>
            <v-layout row justify-space-between>
              <v-flex xs12>
                <img :src="idImgSrc" class="img-responsive" :width="imgWidth" @click="showDialog(true)" style="cursor: pointer;"/>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>
<!-- 
      <v-layout row fill-height>
        <v-flex xs12>
          <v-container fluid>
            <v-layout row justify-space-between>
              <v-flex xs3>
                <v-text-field label="*First name" required hint="*" v-model="member.firstName" v-if="member" ></v-text-field>
              </v-flex>
              <v-flex xs3>
                <v-text-field label="*Last name" required hint="*" v-model="member.lastName" v-if="member" ></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>
 -->      
        <v-layout row fill-height>
          <v-toolbar
            slot="footer"
            class="mt-2"
            color="primary"
            dark
            dense
            flat
          >
          <v-flex xs12>
            <v-spacer></v-spacer>
            <v-btn class="headline" icon @click="prevStep(n)">
              <v-icon x-large>navigate_before</v-icon>
            </v-btn>
            <span class="headline">{{ currentStep }} / {{ steps }}</span>
            <v-btn class="headline" icon @click="nextStep(n)">
              <v-icon x-large>navigate_next</v-icon>
            </v-btn>
          </v-flex>
          </v-toolbar>
        </v-layout>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import MiniCameraDialog from '@/components/MiniCameraDialog.vue'
import { EventBus } from '@/eventBus'
import { Storage } from 'aws-amplify'
import aws_exports from '@/aws-exports'
import { mapState, mapGetters, mapActions } from 'vuex'
import { CallDialog } from '@/utils'
import store from '@/store'

export default {
  name: 'CheckIn',
  data () {
    return {
    }
  },
  created () {
    this.setCurrentStep(1)
  },
  async beforeRouteEnter (to, from, next) {
    const error = await store.dispatch('checkIn/loadMembers').catch(error => {
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  async beforeRouteUpdate (to, from, next) {
    const error = await store.dispatch('checkIn/loadMembers').catch(error => {      
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  computed: {
    imgWidth: {
      get() {
        if (this.$window.width > this.$window.height) {
          return "60%"
        } else {
          return "100%"
        }
      }
    },
    currentStep: {
      get() {
        return this.$store.state.checkIn.currentStep
      },
      set (val) {
        return this.setCurrentStep(val)
      }
    },
    ...mapState({
      members: state => state.checkIn.members,
      idImgSrc: state => state.checkIn.idImgSrc
    }),
    ...mapGetters('checkIn', {
      steps: 'steps',
      member: 'member'
    })
  },
  methods: {
    swipe (direction) {
      if (direction === 'Left') {
        this.goNext()
      } else if (direction === 'Right') {
        this.goPrevious()
      }
    },
    prevStep (n) {
      if (n === 1) {
        this.setCurrentStep(this.steps)

      } else {
        this.setCurrentStep(n - 1)
      }
    },
    nextStep (n) {
      if (n === this.steps) {
        this.setCurrentStep(1)
      } else {
        this.setCurrentStep(n + 1)
      }
    },
    goPrevious () {
      this.prevStep(this.currentStep)
    },
    goNext () {
      this.nextStep(this.currentStep)
    },
    showDialog() {
      EventBus.$emit('MiniCameraDialog', { showDialog: true });
    },
    onCancelClose() {
      console.log('on cancel')
    },
    async onCaptureClose(val) {
      if (val.base64Img) {
        await this.putImage({img: val.base64Img, isId: true})
        await this.setIdImgSrc()        
        
        const error = await this.saveMember(true).catch(e => {
          console.error('saveMember', e)
          return e
        })

        if (error) {
          CallDialog(error)
          return
        } 

        this.goNext()
      }
    },
    ...mapActions({
      setCurrentStep: 'checkIn/setCurrentStep',
      setIdImgSrc: 'checkIn/setIdImgSrc',
      saveMember: 'checkIn/saveMember',
      putImage: 'checkIn/putImage',
      loadMembers: 'checkIn/loadMembers',
    })
  },
  components: {
    cameraDialog: MiniCameraDialog
  }
}
</script>